body {
  position: relative;
  background-image: linear-gradient(
    to bottom,
    rgba(236, 68, 228, 0.314) 35.5%,
    rgba(197, 78, 206, 0.466) 64.5%
  );
}

.list {
  list-style: none;
}
.link {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn {
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.875;
  text-align: center;
  letter-spacing: 0.06em;
  width: 200px;
  height: 50px;
  color: rgb(87, 6, 80);
  background: violet;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  padding: 10px;
  border: none;
  cursor: pointer;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.section {
  margin: 0 auto;
}

.title {
  display: block;
  text-align: center;
  color: rgb(101, 14, 104);
  margin-bottom: 30px;
}

.search-form {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.input {
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.875;
  width: 300px;
  height: 50px;
  padding: 15px;
  color: rgb(87, 6, 80);
  background-color: violet;
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 4px rgba(184, 116, 161, 0.15));
  border-radius: 4px;

  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.input:focus {
  outline: none;
  border-color: rgb(59, 6, 52);
}

.input::placeholder {
  display: inline-block;
  color: rgba(59, 5, 52, 0.6);
}

.search-btn:hover,
.search-btn:focus {
  background-color: rgb(182, 56, 182);
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: -7px;
}

.wrapper-item {
  margin: 7px;
}

.photo-card {
  width: 450px;
  height: 350px;
  margin-right: 30px;
}

.card-img {
  height: 290px;
}

.stats {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.link-photo-card {
  display: block;
  height: auto;
  width: 100%;
}

.photo-card:focus,
.photo-card:hover {
  box-sizing: border-box;
  box-shadow: 0px 1px 1px rgba(138, 9, 99, 0.12), 0px 4px 4px rgba(170, 8, 148, 0.06),
    1px 4px 6px rgba(104, 12, 88, 0.16);
}

.stats-item {
  font-weight: 900;
  color: rgb(80, 23, 85);
}

.material-icons {
  font-size: 30px;
  color: rgb(59, 9, 78);
}

.load-more-btn {
  margin: 0 auto;
  margin-top: 30px;
}

.load-more-btn:hover,
.load-more-btn:focus {
  background-color: rgb(180, 50, 180);
}

///////modal/////

.backdrop {
  position: fixed;
  background: rgba(0, 0, 0, 0.2);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;

  //transition: opacity var(--transition), visibility var(--transition)
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;

  background-color: aquamarine;
  width: 100%;
  max-width: 1100px;
  height: 95%;
  max-height: 609px;
  margin-left: 0;
  margin-right: 0;

  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
